.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;

  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 0 30px 0;

  @include vp-1199 {
    padding-bottom: 29px;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  &.is-invalid {
    label span {
      border: 1px solid $color-wild-watermelon;
    }
  }

  & p {
    margin: 0;
    padding: 0;
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
  }
}

.custom-toggle__label {
  display: flex;
  align-items: center;
  position: relative;
  height: 20px;
  margin: 0;
  padding: 0 0 0 30px;
  cursor: pointer;

  input:disabled {
    cursor: none;

    & + span {
      background: rgba($color-cerulean-blue, 0.2);
    }
  }

  input:checked {
    + span {
      background: $color-default-white;
    }

    ~ svg {
      display: inline;
      stroke: $color-cerulean-blue;
    }
  }

  span {
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    background: $color-cerulean-blue;
    border: 1px solid rgba($color-default-white, 0.7);
    border-radius: 2px;
  }

  svg {
    position: absolute;
    display: none;
    top: 5px;
    left: 5px;
  }

  &:hover:not(:disabled),
  &:focus-within:not(:disabled) {
    span {
      border: 1px solid rgba($color-default-white, 1);
      background: $color-cerulean-blue;
    }

    svg {
      stroke: $color-default-white;
    }

    p {
      color: $color-charcoal;
    }
  }
}

.custom-toggle__label--secondary {

  .custom-toggle__text-wrapper {
    display: flex;
    flex-direction: column;
    color: rgba($color-alabaster, 0.8);
  }

  &:hover:not(:disabled),
  &:focus-within:not(:disabled) {
    p {
      color: $color-default-white;
    }
  }
}

.custom-toggle--secondary {
  padding-bottom: 39px;

  @include vp-767 {
    padding-bottom: 34px;
  }
}


