.programs-slide {
  display: flex;
  flex-direction: column;
  justify-content: end;
  position: relative;
  min-height: 420px;
  margin: 0;
  padding: 20px;
  border-radius: 12px;

  @media (max-width: 1239px) {
    width: 372px;
  }

  @include vp-767 {
    min-height: 400px;
    //padding: 18px 10px;
    padding-top: 137.93%;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 0;

  }
}

.programs-slide-1 {
  background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/1/program-1.webp") 50% 50% / cover no-repeat;

  @media (min-resolution: $retina-dppx) {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/1/program-1@2x.webp") 50% 50% / cover no-repeat;
  }

  @include vp-1199 {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/1/program-1-tablet.webp") 50% 50% / cover no-repeat;

    @media (min-resolution: $retina-dppx) {
      background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/1/program-1-tablet@2x.webp") 50% 50% / cover no-repeat;
    }
  }

  @include vp-767 {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/1/program-1-mobile.webp") 50% 0 / cover no-repeat;

    @media (min-resolution: $retina-dppx) {
      background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/1/program-1-mobile@2x.webp") 50% 0 / cover no-repeat;
    }
  }
}

.programs-slide-2 {
  background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/2/program-2.webp") 50% 50% / cover no-repeat;

  @media (min-resolution: $retina-dppx) {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/2/program-2@2x.webp") 50% 50% / cover no-repeat;
  }

  @include vp-1199 {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/2/program-2-tablet.webp") 50% 50% / cover no-repeat;

    @media (min-resolution: $retina-dppx) {
      background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/2/program-2-tablet@2x.webp") 50% 50% / cover no-repeat;
    }
  }

  @include vp-767 {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/2/program-2-mobile.webp") 50% 50% / cover no-repeat;

    @media (min-resolution: $retina-dppx) {
      background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/2/program-2-mobile@2x.webp") 50% 50% / cover no-repeat;
    }
  }
}

.programs-slide-3 {
  background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/3/program-3.webp") 50% 50% / cover no-repeat;

  @media (min-resolution: $retina-dppx) {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/3/program-3@2x.webp") 50% 50% / cover no-repeat;
  }

  @include vp-1199 {
    background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/3/program-3-tablet.webp") 50% 50% / cover no-repeat;

    @media (min-resolution: $retina-dppx) {
      background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/3/program-3-tablet@2x.webp") 50% 50% / cover no-repeat;
    }
  }

  @include vp-767 {
    @include vp-1199 {
      background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/3/program-3-mobile.webp") 50% 50% / cover no-repeat;

      @media (min-resolution: $retina-dppx) {
        background: rgba($color-cerulean-blue, 0.5) url("./../../img/program/3/program-3-mobile@2x.webp") 50% 50% / cover no-repeat;
      }
    }
  }
}

.programs-slide::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(49, 109, 194, 0.2);
  border-radius: 12px;
}

.programs-slide__inner {
  width: 321px;
  background: transparent;
  margin: 0;
  padding: 0;
  position: relative;
  z-index: 1;

  @include vp-1199 {
    width: 263px;
  }

  @include vp-767 {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: end;
    width: 100%;
    padding: 18px 10px;
  }
}

.programs-slide__inner-title {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: fit-content;
  min-width: 194px;
  margin: 0;
  padding: 13px 10px 30px 18px;
  background: $color-default-white;
  border-radius: 12px;
  position: relative;
  z-index: -1;

  @include vp-1199 {
    padding-top: 11px;
    min-width: 173px;
  }

  @include vp-767 {
    min-width: 146px;
    padding: 18px 10px 35px 18px;
  }
}

.programs-slide__inner-title::after {
  content: "";
  position: absolute;
  bottom: 28px;
  right: -19px;
  width: 20px;
  height: 20px;
  background: url("./../../img/svg/first-radius.svg");
}

.programs-slide__inner-title span {
  margin: 0;
  padding: 0;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;

  @include vp-1199 {
    font-size: 20px;
    line-height: 24px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 18px;
  }
}

.programs-slide__inner-footer {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 16px;
  min-height: 79px;
  margin: -28px 0 0;
  padding: 11px 18px 14px;
  border-radius: 12px;
  background: $color-default-white;

  @include vp-1199 {
    margin-top: -29px;
    padding: 9px 18px 10px;
  }

  @include vp-767 {
    min-height: 69px;
    padding: 12px 18px 12px;
  }
}

.programs-slide__inner-footer-text {
  align-self: center;
  margin: 0;
  padding: 0;
}

.programs-slide__inner-footer-text p {
  margin: 0;
  padding: 0;
  color: $color-charcoal;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;

  @include vp-1199 {
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 14px;
  }
}

.programs-slide__inner-footer-link {
  width: 30px;
  height: 30px;
  margin: 12px 0 0;

  @include vp-767 {
    margin: 0;
  }
}
